@fontFamily: 'bpmn-js-pp';
@primary-color: rgb(255, 105, 223);
@primary-opacity-color: rgba(255, 105, 180, 0.2);
@primary-hover-color: hotpink;
@header-height: 48px;


@font-face {
  font-family: @fontFamily;
  src: url('data:;base64,');
  src:
    url('data:;base64,') format('embedded-opentype'),
    url('data:application/font-woff;base64,') format('woff'),
    url('data:application/x-font-ttf;base64,') format('truetype'),
    url('data:image/svg+xml;base64,') format('svg');
  font-weight: normal;
  font-style: normal
}
:global(.highlight-overlay) {
  border: 1px solid #000;
  opacity: 0.4;
  pointer-events: none;
}
// 头部
.header {
  width: 100%;
  height: @header-height;
  background-color: @primary-color;
  position: fixed;
  z-index: 1001;
}

.container {
  position: relative;
  top: @header-height;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  flex: auto;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif !important;
  font-size: 12px;
  border: 1px solid #ddd;
  background: #fff;
  // 折叠按钮
  :global(.properties-panel-fold) {
    position: absolute;
    right: 259px;
    top: 0;
    z-index: 11;
    height: 30px;
    width: 20px;
    cursor: pointer;
    border-right: 0;
    background-color: #fff;
    transition: all 200ms ease-in-out;
    &:before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: url('../resources/icons/fold.svg') 0 0 no-repeat;
    }
    &:global(.fold) {
      right: 0;
      &:before {
        background: url('../resources/icons/unfold.svg') 0 0 no-repeat;
      }
    }
  }
  .canvas {
    height: 100%;
    // background: #ededf4;
    :global {
      .hidden {
        display: none
      }
      .bjs-container {
        display: flex;
        justify-content: start;
        align-items: flex-start
      }
      .djs-palette {
        position: relative;
        top: 0;
        left: 0;
        width: 186px;
        height: 100%;
        flex-shrink: 0;
        transition: width 200ms ease-in-out;
        border-radius: 0;
        border: 0;
        background: #f8f8f8;
        z-index: 1000;
        box-shadow: 0 1px 4px rgba(0, 0, 0, .3);
        &.retract {
          width: 66px;
          .djs-collapse-header {
            display: none
          }
          .djs-collapse-content {
            display: block
          }
          .entry {
            .content-icon {
              &:before {
                font-size: 24px, 
              }
            }
          }
          .djs-palette-toggle {
            &:before {
              background: url(../resources/icons/fold.svg) 0 0 no-repeat;
            }
          }
        }
      }
      .djs-palette-entries {
        padding-top: 10px;
        padding-bottom: 95px;
        height: 100%;
        overflow-y: auto;
        .djs-collapse-header {
          position: relative;
          padding-left: 25px;
          height: 26px;
          line-height: 26px;
          font-size: 14px;
          color: #000;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          cursor: pointer;
          &:hover {
            -webkit-transition: color .3s cubic-bezier(.645,.045,.355,1),border-color .3s cubic-bezier(.645,.045,.355,1),background .3s cubic-bezier(.645,.045,.355,1),padding .15s cubic-bezier(.645,.045,.355,1);
            transition: color .3s cubic-bezier(.645,.045,.355,1),border-color .3s cubic-bezier(.645,.045,.355,1),background .3s cubic-bezier(.645,.045,.355,1),padding .15s cubic-bezier(.645,.045,.355,1);
            background-color: @primary-opacity-color;
            color: @primary-hover-color;
          }
          &:before {
            content: "\e600";
            position: absolute;
            left: 0;
            transform: rotate(-180deg);
            transition: all 200ms ease-in-out;
            background-size: 100%;
            // background-color: #33b371;
          }
          &.open {
            &:before {
              transform: rotate(0);
            }
          }
        }
        .djs-collapse-content {
          display: none;
          &.open {
            display: block
          }
        }
        .entry {
          float: none;
          display: flex;
          align-items: center;
          padding: 0 22px;
          width: 100%;
          height: 28px;
          line-height: unset;
          text-align: left;
          cursor: pointer;
          .content-icon {
            position: relative;
            width: 24px;
            height: 20px;
            flex-shrink: 0;
            img {
              width: 20px;
              height: 20px;
              vertical-align: top;
            }
          }
          .content-icon:before {
            position: absolute;
            top: 0;
            left: 0;
            margin: 0;
            font-size: 20px
          }
          .content-label {
            display: inline-block;
            height: 28px;
            line-height: 28px;
            font-size: 12px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap
          }
        }
      }
      .djs-palette-toggle {
        display: block;
        position: absolute;
        top: 0;
        right: -20px;
        height: 30px;
        width: 20px;
        cursor: pointer;
        border-right: 0;
        background: transparent;
        &:before {
          content: '';
          width: 100%;
          height: 100%;
          position: absolute;
          right: 13px;
          background: url(../resources/icons/unfold.svg) 0 0 no-repeat;
        }
      }
    }
  }
  :global {
    // 缩略图
    .djs-minimap {
      position: absolute;
      right: 296px;
      top: 20px;
      overflow: hidden;
      background-color: rgba(255, 255, 255, 0.9);
      border: solid 1px #CCC;
      border-radius: 2px;
      box-sizing: border-box;
      user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      -webkit-user-select: none;
      .map {
        display: none;
        width: 200px;
        height: 112px;
        cursor: crosshair;
      }
      .viewport {
        /* fill: rgba(255, 116, 0, 0.25); */
        fill: none;
        stroke: none;
      }
      .viewport-dom {
        position: absolute;
        border: dashed 2px @primary-color;
        border-radius: 2px;
        box-sizing: border-box;
        cursor: move;
      }
      .cursor-crosshair {
        cursor: crosshair;
      }

      .cursor-move {
        cursor: move;
      }
      &:not(.open) {
        overflow: hidden;
        .toggle {
          width: 38px;
          height: 38px;
          line-height: 38px;
          box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
          cursor: pointer;
          position: relative;
          text-align: center;
          line-height: 38px;
          &:before {
            content: '';
            position: absolute;
            width: 25px;
            height: 25px;
            top: 6px;
            right: 6px;
            background: url(../resources/icons/mapOpen.svg) 0 0 no-repeat;
            background-size: cover;
          }
        }
        .viewport-dom {
          display: none;
        }
      } 
      &.open {
        .map {
          display: block;
        }
        .toggle {
          position: absolute;
          right: 0;
          z-index: 1;
          width: 38px;
          height: 38px;
          line-height: 38px;
          text-align: center;
          cursor: pointer;
          &:before {
            content: '';
            position: absolute;
            width: 20px;
            height: 20px;
            top: 5px;
            left: 10px;
            background: url(../resources/icons/mapClose.svg) 0 0 no-repeat;
            background-size: cover;
          }
        }
        .overlay {
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          background: rgba(255, 255, 255, 0.2);
          pointer-events: none;
        }
      }
    }

    // 任务属性
    .tp-priorities {
      position: absolute;
      right: 296px;
      top: 150px;
      font-family: Helvetica, Arial;
      background: #F3F4F3;
      padding: 5px;
      .tp-value.high {
        font-weight: bold;
      }
    }

    // 捕捉线
    .djs-snap-line {
      stroke: @primary-color;
      stroke: @primary-opacity-color;
    }

    .djs-lasso-overlay {
      fill: @primary-color;
      stroke: @primary-color;
    }

    // 拖拽时shape样式
    // .djs-shape .djs-visual,
    // .djs-connection .djs-visual,
    .djs-dragger .djs-visual
    {
      circle, path, polygon, polyline, rect, text {
        stroke: @primary-color !important;
      }
    }

    // shape右侧选项
    .djs-context-pad {
      background-color: #F8F8F8;
      border-radius: 4px;
      // padding: 4px;
      &.open {
        color: @primary-color;
        // width: 56px;
        // max-width: 104px;
        // &:before {
        //   position: absolute;
        //   content: " ";
        //   border: transparent 12px solid;
        //   border-right-color: #F8F8F8;
        //   top: 50%;
        //   margin-top: -12px;
        //   left: -1.5em;
        //   height: 0;
        //   width: 0;
        // }
      }
      .entry {
        &.icon-red {
          background-color: hotpink;
        }
        &:hover {
          background: @primary-opacity-color;
        }
      }
    }
    
    .bjs-container {
      .bjs-powered-by {
        display: none;
      }
      
      .djs-palette {
        .djs-palette-entries {
          .group {
            .entry {
              &.highlighted-entry {
                color: @primary-hover-color !important;
              }
              &:hover {
                color: @primary-color;
              }
            }
          }
        }
      }

      .djs-group {
        .djs-element {
          &:hover {
            .djs-outline {
              stroke: @primary-hover-color;
            }
          }
          &.selected {
            .djs-outline {
              stroke: @primary-color;
            }
          }
        }
      }
    }
    .bpp-properties-panel {
      background-color: #f8f8f8;
      position: relative;
      height: 100%
    }
    .bpp-properties-panel:empty {
      display: none
    }
    .bpp-properties-panel label,
    .bpp-properties-panel input {
      vertical-align: middle
    }
    .bpp-properties-panel input,
    .bpp-properties-panel button,
    .bpp-properties-panel textarea,
    .bpp-properties-panel [contenteditable] {
      padding: 3px 6px;
      border: 1px solid #ccc
    }
    .bpp-properties-panel input:focus,
    .bpp-properties-panel button:focus,
    .bpp-properties-panel textarea:focus,
    .bpp-properties-panel [contenteditable]:focus {
      outline: none;
      border-color: rgb(81, 180, 19);
      -webkit-box-shadow: 0 0 1px 2px rgba(82, 180, 21, 0.2);
      box-shadow: 0 0 1px 2px rgba(82, 180, 21, 0.2)
    }
    .bpp-properties-panel input.invalid,
    .bpp-properties-panel button.invalid,
    .bpp-properties-panel textarea.invalid,
    .bpp-properties-panel [contenteditable].invalid {
      border-color: #ca3232;
      background: #f0c2c2
    }
    .bpp-properties-panel input.invalid:focus,
    .bpp-properties-panel button.invalid:focus,
    .bpp-properties-panel textarea.invalid:focus,
    .bpp-properties-panel [contenteditable].invalid:focus {
      -webkit-box-shadow: 0 0 1px 2px rgba(204, 51, 51, 0.2);
      box-shadow: 0 0 1px 2px rgba(204, 51, 51, 0.2)
    }
    .bpp-properties-panel [type='text'],
    .bpp-properties-panel [contenteditable],
    .bpp-properties-panel textarea,
    .bpp-properties-panel select {
      width: 100%
    }

    .bpp-properties-panel select {
      padding: 2px 0 6px
    }
    .bpp-properties-panel [contenteditable],
    .bpp-properties-panel textarea {
      resize: vertical
    }
    .bpp-properties-panel [contenteditable] {
      outline: 0 solid transparent;
      background-color: white;
      overflow-y: auto;
      white-space: pre-wrap;
      /* css-3 */
      white-space: -moz-pre-wrap;
      /* Mozilla, since 1999 */
      white-space: -pre-wrap;
      /* Opera 4-6 */
      white-space: -o-pre-wrap;
      /* Opera 7 */
      word-wrap: break-word;
      /* Internet Explorer 5.5+ */
    }
    .bpp-properties-panel [contenteditable]:before {
      content: '\feff '
    }
    .bpp-properties-panel [disabled] {
      color: #808080
    }
    .bpp-properties-panel label {
      font-weight: bolder;
      display: inline-block;
      vertical-align: middle;
      color: #666;
      margin-bottom: 3px
    }
    .bpp-properties-panel label[for] {
      cursor: pointer
    }
    .bpp-properties-panel label.bpp-hidden {
      display: none
    }
    .bpp-properties-panel .entry-label {
      font-weight: bolder;
      display: inline-block;
      vertical-align: middle;
      color: #666;
      font-size: 120%;
      margin-top: 5px;
      margin-bottom: 10px;
      -webkit-transition: margin 0.218s linear;
      transition: margin 0.218s linear;
      font-style: italic
    }
    .bpp-properties-panel .entry-label[for] {
      cursor: pointer
    }
    .bpp-properties-panel .entry-label.bpp-hidden {
      display: none
    }
    .bpp-properties-panel .entry-label.divider {
      border-top: 1px dotted #ccc;
      padding-top: 8px;
      margin-top: 16px;
      width: 100%
    }
    .bpp-properties-panel button {
      position: absolute;
      top: 0;
      height: 23px;
      width: 24px;
      overflow: hidden;
      cursor: pointer;
      background-color: #f8f8f8;
      border: 1px solid #ccc
    }
    .bpp-properties-panel button > span {
      display: none
    }
    .bpp-properties-panel button:before {
      font-family: @fontFamily;
      font-style: normal;
      font-weight: normal;
      // speak: none;
      display: inline-block;
      text-decoration: inherit;
      text-align: center;
      font-variant: normal;
      text-transform: none;
      line-height: 1em;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      position: relative
    }
    .bpp-properties-panel button.add:before {
      content: '\E803'
    }
    .bpp-properties-panel button.clear:before {
      content: '\E802'
    }
    .bpp-properties-panel button:hover {
      color: rgb(81, 180, 19)
    }
    .bpp-error-message,
    .bpp-error-message.bpp-entry-link {
      margin-top: 5px;
      color: #d13434
    }
    .bpp-row {
      margin-top: 10px;
      margin-bottom: 10px;
      &:first-of-type {
        margin-top: 0
      }
      &:last-of-type {
        margin-bottom: 0
      }
    }
    .bpp-textfield {
      margin-bottom: 3px;
      input {
        padding-right: 28px
      }
      .clear {
        background: transparent;
        border: none;
        top: 0;
        right: 0
      }
    }
    .bpp-textbox {
      margin-bottom: 3px
    }
    .bpp-radios-group {
      list-style: none;
      padding: 0;
      margin: 0 0 9px 0;
      .bpp-radio-wrapper {
        margin: 6px 0
      }
      input, label {
        vertical-align: middle
      }
      input {
        margin-top: 0;
        margin-left: 0
      }
      label {
        &:after {
          display: none
        }
      }
    }
    .bpp-checkbox {
      input {
        margin-left: 0
      }
      label {
        &:after {
          display: none
        }
      }
    }
    .bpp-combo-input {
      margin-top: 7px
    }
    .bpp-select select {
      height: 23px
    }
    .bpp-select button.get-data {
      top: unset;
      bottom: 26px;
      right: 0;
      width: unset
    }
    .bpp-select button.add {
      top: -22px;
      right: 0
    }
    .bpp-select button.add,
    .bpp-element-list button.add,
    .bpp-select button.clear,
    .bpp-element-list button.clear {
      top: -23px;
      border-bottom: none
    }
    .bpp-select button.add,
    .bpp-element-list button.add {
      right: 0
    }
    .bpp-select button.clear,
    .bpp-element-list button.clear {
      right: 23px
    }
    .bpp-properties-entry {
      margin-bottom: 9px
    }
    .bpp-properties-entry .bpp-field-wrapper {
      position: relative
    }
    .bpp-properties-entry .bpp-field-wrapper input[readonly] + .clear {
      display: none
    }
    .bpp-properties-entry .bpp-field-wrapper select {
      resize: vertical
    }
    .bpp-hidden {
      display: none
    }
    label.bpp-hidden {
      display: none
    }
    .bpp-table-add-row > button {
      position: relative;
      margin-left: 10px
    }
    .bpp-table {
      margin-top: 10px
    }
    .bpp-table-row {
      margin-bottom: 2px;
      overflow: hidden
    }
    .bpp-table-row > input,
    .bpp-table-row > button {
      float: left
    }
    .bpp-table-row > label {
      padding-left: 5px
    }
    .bpp-table-row > label.bpp-table-row-columns-1,
    .bpp-table-row > input.bpp-table-row-columns-1 {
      width: 100%
    }
    .bpp-table-row > label.bpp-table-row-columns-1.bpp-table-row-removable,
    .bpp-table-row > input.bpp-table-row-columns-1.bpp-table-row-removable {
      width: calc(100% - 24px)
    }
    .bpp-table-row > label.bpp-table-row-columns-2,
    .bpp-table-row > input.bpp-table-row-columns-2 {
      width: 50%
    }
    .bpp-table-row > label.bpp-table-row-columns-2.bpp-table-row-removable,
    .bpp-table-row > input.bpp-table-row-columns-2.bpp-table-row-removable {
      width: calc(50% - 12px)
    }
    .bpp-table-row > label.bpp-table-row-columns-2:nth-child(2),
    .bpp-table-row > input.bpp-table-row-columns-2:nth-child(2) {
      border-left: none
    }
    .bpp-table-row > button {
      border-left: none;
      position: static
    }
    .bpp-properties-static {
      margin-bottom: 0;
      margin-top: 0;
      border: 1px solid #ccc;
      background-color: white;
      padding: 3px 6px;
      font: 13.3333px;
      width: 100%
    }
    .bpp-entry-link {
      cursor: pointer;
      color: rgb(82, 180, 21)
    }
    .bpp-icon-warning:before {
      font-family: @fontFamily;
      font-style: normal;
      font-weight: normal;
      // speak: none;
      display: inline-block;
      text-decoration: inherit;
      text-align: center;
      font-variant: normal;
      text-transform: none;
      line-height: 1em;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      content: '\E806'
    }
    .bpp-field-description {
      margin-top: 5px;
      color: #999
    }
    .bpp-field-description a {
      color: #3d8610;
      text-decoration: none
    }
    .bpp-field-description a:hover {
      color: rgb(82, 180, 21)
    }
    .bpp-properties-header {
      padding: 15px;
      padding-bottom: 5px
    }
    .bpp-properties-header > .label {
      display: none;
      font-size: 120%;
      font-weight: bolder
    }
    .bpp-properties-header > .search {
      display: none;
      margin-top: 5px;
      position: relative
    }
    .bpp-properties-header > .search input {
      position: relative;
      border-radius: 15px;
      width: 100%;
      z-index: 1
    }
    .bpp-properties-header > .search button {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      border: none;
      background-color: transparent;
      z-index: 2
    }
    .bpp-properties-header > .search button:before {
      content: '\E805'
    }
    .bpp-properties-group {
      padding: 6px 15px 6px 15px;
      position: relative;
      max-height: 2000px;
      overflow: hidden;
      transition: max-height 0.218s ease-in-out, padding-top 0.218s ease-in-out, padding-bottom 0.218s ease-in-out
    }
    .bpp-properties-group:empty {
      display: none
    }
    .bpp-properties-group > .group-toggle {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      width: 4px;
      cursor: pointer;
      -webkit-transition: background-color 0.218s linear;
      transition: background-color 0.218s linear
    }
    .bpp-properties-group > .group-toggle:hover {
      background-color: #8fc071
    }
    .bpp-properties-group > .group-label {
      font-weight: bolder;
      display: inline-block;
      vertical-align: middle;
      color: #666;
      font-size: 120%;
      margin-top: 5px;
      margin-bottom: 10px;
      -webkit-transition: margin 0.218s linear;
      transition: margin 0.218s linear;
      font-style: italic
    }
    .bpp-properties-group > .group-label[for] {
      cursor: pointer
    }
    .bpp-properties-group > .group-label.bpp-hidden {
      display: none
    }
    .bpp-properties-group:hover > .group-toggle {
      background-color: #ccc
    }
    .bpp-properties-group:hover > .group-toggle:hover {
      background-color: #8fc071
    }
    .bpp-properties-group.group-closed {
      // max-height: 20px;
      max-height: 26px;
      border-top: none;
      cursor: pointer;
      background-color: rgba(143, 192, 113, 0.2);
      padding-top: 0;
      padding-bottom: 0
    }
    .bpp-properties-group.group-closed > div {
      visibility: hidden
    }
    .bpp-properties-group.group-closed > .group-label {
      margin-top: 2px;
      margin-bottom: 2px
    }
    .bpp-properties-group.group-closed:hover > .group-label {
      color: rgb(82, 180, 21)
    }
    .bpp-properties-group + .bpp-properties-group {
      border-top: 1px dotted #ccc
    }
    .bpp-properties-group:last-child {
      padding-bottom: 9px
    }
    .cam-add-listener > button {
      position: relative;
      margin-left: 10px
    }
    [data-list-entry-container] > .bpp-listener-area {
      border: 1px solid #ccc;
      margin: 10px 1px;
      padding: 10px
    }
    .bpp-listener-area {
      position: relative
    }
    .bpp-listener-area > button {
      position: absolute;
      right: 0;
      top: 0;
      border: none;
      background: none
    }
    .bpp-listener-area + .bpp-listener-area {
      margin-top: 20px
    }
    .bpp-properties-tab-bar {
      border-bottom: 1px solid #ccc;
      padding: 0 15px;
      .scroll-tabs-button {
        cursor: pointer;
        font-size: 16px;
        padding: 3px 4px 3px 4px;
        color: #666;
        user-select: none;
        transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
        &:hover {
          font-weight: bold;
          background-color: @primary-opacity-color;
        }
        &.scroll-tabs-left {
          float: left;
          margin-left: -15px
        }
        &.scroll-tabs-right {
          float: right;
          margin-right: -15px
        }
      }
      &:not(.scroll-tabs-overflow) {
        .scroll-tabs-button {
          display: none
        }
      }
    }
    ul.bpp-properties-tabs-links {
      margin: 5px 0 -1px 0;
      padding: 0;
      white-space: nowrap;
      overflow: hidden;
      &> li {
        display: inline-block;
        margin: 0;
        &.bpp-hidden {
          display: none
        }
        &> a {
          display: inline-block;
          font-size: 12px;
          padding: 4px 7px;
          border: 1px solid #ccc;
          border-radius: 3px 3px 0 0;
          border-bottom: transparent;
          background-color: #f8f8f8;
          color: #666;
          text-decoration: none;
          &:hover {
            color: #4d4d4d
          }
        }
        &+ li {
          margin-left: 4px
        }
        &.bpp-active { 
          a {
            padding-bottom: 5px;
            border-top: 2px solid rgb(82, 180, 21);
            border-bottom: none
          }
        }
      }
    }
    .bpp-properties-tab {
      display: none;
      &.bpp-hidden {
        display: none;
      }
      &.bpp-active {
        display: block
      }
    }
    .message {
      width: 100%;
      height: 100%;
      text-align: center;
      display: table;
      font-size: 16px;
      color: #111;
      .note {
        vertical-align: middle;
        text-align: center;
        display: table-cell
      }
      .error {
        .details {
          max-width: 500px;
          font-size: 12px;
          margin: 20px auto;
          text-align: left;
        }
        pre {
          border: solid 1px rgb(204, 204, 204);
          background: rgb(238, 238, 238);
          padding: 10px
        }
      }
    }
     // 属性面板
    .properties-panel-parent {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      width: 260px;
      z-index: 10;
      // border-left: 1px solid #ccc;
      overflow: hidden;
      transition: all 200ms ease-in-out;
      box-shadow: 0 1px 4px rgba(0, 0, 0, .3);
      &.hidePanel {
        right: -261px; 
      }
      &:empty {
        display: none;
      }
      > .djs-properties-panel {
        padding-bottom: 70px;
        min-height:100%;
      }
      .bpp-properties-panel {
        .bpp-properties {
          height: 100%;
          .bpp-properties-tab-bar {
            .bpp-properties-tabs-links {
              &> li.bpp-active { 
                a {
                  border-top-color: @primary-color;
                }
              }
            }
          }
          .bpp-properties-tabs-container {
            height: 100%;
            overflow: auto;
            width: 100%;
            padding-bottom: 150px;
            .bpp-properties-tab {
              .bpp-properties-group {
                width: 100%;
                .group-label {
                  font-style: normal;
                }
                &.group-closed {
                  background-color: @primary-opacity-color;
                  &:hover {
                    > .group-label {
                      color: @primary-color;
                    }
                  }
                }
                &:hover {
                  .group-toggle {
                    background-color: @primary-opacity-color;
                  }
                }
                .group-toggle {
                  &:hover {
                    background-color: @primary-color;
                  }
                }
                .bpp-properties-entry {
                  .bpp-field-wrapper {
                    [contenteditable="false"] {
                      border: 0 none;
                      background-color: #F8F8F8;
                      resize: none;
                    }
                    input {
                      font-size: 12px;
                      &[disabled="disabled"] {
                        border: 0 none;
                        background-color: #F8F8F8;
                      }
                      &+button {
                        display: none;
                      }
                    }
                    select {
                      &:focus {
                        outline-color: @primary-color;
                      }
                    }
                  }
                }
              }
            }
          }
        }
        input, button, textarea, [contenteditable] {
          -webkit-transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
          transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
          &:hover {
            color: @primary-hover-color;
            border-color: @primary-color;
          }
          &:focus {
            border-color: @primary-color;
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);
          }
        }
      }
    }
    .djs-bendpoint .djs-visual {
      fill: @primary-opacity-color;
      stroke-width: 1px;
      stroke-opacity: 0.5;
      stroke: black;
    }
    // .djs-segment-dragger .djs-visual {
    //   fill: @primary-hover-color;
    //   stroke-width: 1px;
    //   stroke-opacity: 1;
    //   stroke: rgba(255, 255, 121, 0.2);
    // }
    .djs-popup .entry:hover, .djs-popup .entry.active:hover {
      background: @primary-opacity-color;
    }

    .djs-popup .djs-popup-header .entry.active {
      color: @primary-hover-color;
      border: solid 1px @primary-hover-color;
      background-color: @primary-opacity-color;
    }
  }
}